.inputImage {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: aqua;
  border: solid 1px;
  cursor: "pointer";
  position: relative;
  margin: 0 auto;
  padding: 0;
}
.selectedImage {
  width: 20px;
  height: 20px;
  border: solid 1px;
  border-radius: 50%;
}
