.screen {
  position: relative;
  margin: 0 auto;
  padding: 0;
  background-color: #bacee0;
  display: flex;
  flex-flow: column wrap;
  align-items: flex-end;
  height: 600px;
  width: 400px;
  border: solid 1px;
  /* .screen을 relative로 설정 */
}

.input_field {
  padding: 0;
  margin: 0 auto;
  min-width: 398px;
  max-width: 398px;
  height: 100px;
  font-size: 16px;
  border: 1px solid #ddd;
  z-index: 1;
  position: absolute; /* .input_field를 absolute로 설정 */
  bottom: 0; /* 하단으로 정렬 */
  left: 50%; /* 가운데 정렬을 위해 50%로 설정 */
  transform: translateX(-50%); /* 가운데 정렬을 위해 왼쪽으로 50% 이동 */
}

.add_button {
  width: 70px;
  height: 30px;
  font-family: "Helvetica", "Arial", sans-serif;
  font-size: 12px;
  padding: 7px;
  cursor: pointer;
  background-color: #f9e000;
  border: 0px solid;
  padding: 0;
  text-transform: uppercase;
  position: absolute; /* .input_field를 absolute로 설정 */
  bottom: 3px; /* 하단으로 정렬 */

  left: 99%; /* 가운데 정렬을 위해 50%로 설정 */
  transform: translateX(-100%); /* 가운데 정렬을 위해 왼쪽으로 50% 이동 */
  float: right;
  z-index: 2;
}

.add_button:hover {
  background-color: darkgoldenrod;
}

.todo_list {
  position: relative;
  margin: 0px;
  max-width: 350px;
  max-height: 420px;
  background-color: #bacee0;
  list-style-type: none;
  padding: 0;
  overflow: auto; /* 스크롤 활성화 */
  overflow-x: hidden; /* 가로 스크롤은 안보이게 */
  top: 8%;
  z-index: 0;
}

.todo_item {
  text-align: left;
  background-color: #f9e000;
  border-radius: 3px;
  font-size: 15px;
  color: black;
  top: 0%;
  margin-bottom: 10px;
  margin-right: 2%;
  padding: 8px 12px 8px 8px;
  margin-bottom: 20px;
}

